const getFormatDate = (date: any) => {
  /** 判断日期是否为YYYYMMDD格式*/
  const toDateFromString = (strDate: any) => {
    if (strDate.length !== 8) {
      return false;
    }
    let dtDate = null;
    const nYear = parseInt(strDate.substring(0, 4), 10);
    const nMonth = parseInt(strDate.substring(4, 6), 10);
    const nDay = parseInt(strDate.substring(6, 8), 10);
    if (isNaN(nYear) || isNaN(nMonth) || isNaN(nDay)) {
      return false;
    }
    dtDate = new Date(nYear, nMonth - 1, nDay);
    if (
      nYear != dtDate.getFullYear() ||
      nMonth - 1 != dtDate.getMonth() ||
      nDay != dtDate.getDate()
    ) {
      return false;
    }
    return dtDate;
  };

  /** 判断日期是否为YYYYMMDD格式*/
  date = toDateFromString(date);

  let newDate: any;
  const type = Object.prototype.toString.call(date);
  if (type === "[object Date]") {
    newDate = date;
  } else if (type === "[object String]" || type === "[object Number]") {
    newDate = new Date(date);
  } else {
    newDate = new Date();
  }

  /** 获取对应格式的日期 **/
  const dateFormat = (format = "") => {
    const Y = newDate.getFullYear();
    const M =
      newDate.getMonth() + 1 > 9
        ? newDate.getMonth() + 1
        : `0${newDate.getMonth() + 1}`;
    const D =
      newDate.getDate() > 9 ? newDate.getDate() : `0${newDate.getDate()}`;

    const h =
      newDate.getHours() > 9 ? newDate.getHours() : `0${newDate.getHours()}`;

    const m =
      newDate.getMinutes() > 9
        ? newDate.getMinutes()
        : `0${newDate.getMinutes()}`;
    const s =
      newDate.getSeconds() > 9
        ? newDate.getSeconds()
        : `0${newDate.getSeconds()}`;

    let time: any;
    if (format === "YYYY-MM-DD") {
      time = `${Y}-${M}-${D}`;
    } else if (format === "YYYY/MM/DD") {
      time = `${Y}/${M}/${D}`;
    } else if (format === "YYYY.MM.DD") {
      time = `${Y}.${M}.${D}`;
    } else if (format === "YYYY-MM-DD hh:mm:ss") {
      time = `${Y}-${M}-${D} ${h}:${m}:${s}`;
    } else if (format === "YYYY/MM/DD hh:mm:ss") {
      time = `${Y}/${M}/${D} ${h}:${m}:${s}`;
    } else if (format === "YYYY.MM.DD hh:mm:ss") {
      time = `${Y}.${M}.${D} ${h}:${m}:${s}`;
    } else if (format === "YYYY-MM-DD hh:mm") {
      time = `${Y}-${M}-${D} ${h}:${m}`;
    } else if (format === "YYYY/MM/DD hh:mm") {
      time = `${Y}/${M}/${D} ${h}:${m}`;
    } else if (format === "YYYY.MM.DD hh:mm") {
      time = `${Y}.${M}.${D} ${h}:${m}`;
    } else {
      time = `${Y}-${M}-${D}`;
    }
    return time;
  };

  return { dateFormat };
};

// 根据前17位生成末位
// 生成随机数字，max限制最大值，base限制最小值
const getRandom = (max: number, base: number) => {
  return Math.floor(Math.random() * max + (base ? base : 0));
};

// 根据前17位生成末位
const cnNewID = (idcard: string) => {
  const arrExp = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2]; // 加权因子
  const arrValid = [1, 0, "X", 9, 8, 7, 6, 5, 4, 3, 2]; // 校验码
  let sum = 0,
    idx;
  for (let j = 0; j < 17; j++) {
    // 对前17位数字与权值乘积求和
    sum += parseInt(idcard[j], 10) * arrExp[j];
  }
  return arrValid[sum % 11];
};

// 生成身份证号
const getIdcard = () => {
  let idcard = "";
  for (let i = 0; i < 18; i++) {
    if (i < 6) {
      idcard += getRandom(10, 0);
    } else if (i == 6) {
      idcard += getRandom(2, 1); //年份第一位仅支持1和2
    } else if (i == 7) {
      idcard += idcard[6] == "1" ? 9 : 0; //两位年份规则，仅支持19和20
    } else if (i == 8) {
      idcard += idcard[6] == "1" ? getRandom(7, 3) : getRandom(2, 0); //三位年份规则，仅支持193-199、200、201这些值
    } else if (i == 9) {
      idcard += getRandom(10, 0); //四位年份规则,0-9
    } else if (i == 10) {
      idcard += getRandom(2, 0); //首位月份规则
    } else if (i == 11) {
      idcard += idcard[10] == "0" ? getRandom(9, 1) : getRandom(3, 0); //末位月份规则
    } else if (i == 12) {
      const a = idcard.substr(6, 4);
      const b = idcard.substr(10, 2);
      const maxDays = new Date(Number(a), Number(b), 0).getDate(); // 获取当月最大天数
      const day = getRandom(maxDays, 1);
      idcard += day < 10 ? "0" + day : day;
      i++;
    } else if (i > 13 && i < 17) {
      idcard += getRandom(10, 0);
    } else if (i == 17) {
      idcard += cnNewID(idcard);
    }
  }
  return idcard;
};

const isCardIDValid = (code: any) => {
  const city = {
    11: "北京",
    12: "天津",
    13: "河北",
    14: "山西",
    15: "内蒙古",
    21: "辽宁",
    22: "吉林",
    23: "黑龙江 ",
    31: "上海",
    32: "江苏",
    33: "浙江",
    34: "安徽",
    35: "福建",
    36: "江西",
    37: "山东",
    41: "河南",
    42: "湖北 ",
    43: "湖南",
    44: "广东",
    45: "广西",
    46: "海南",
    50: "重庆",
    51: "四川",
    52: "贵州",
    53: "云南",
    54: "西藏 ",
    61: "陕西",
    62: "甘肃",
    63: "青海",
    64: "宁夏",
    65: "新疆",
    71: "台湾",
    81: "香港",
    82: "澳门",
    91: "国外 ",
  };
  let tip = "";
  let pass = true;
  if (
    !code ||
    !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(
      code
    )
  ) {
    tip = "身份证号格式错误";
    pass = false;
  } else {
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    if (!city[code.substr(0, 2)]) {
      tip = "身份证地址编码错误";
      pass = false;
    } else {
      // 18位身份证需要验证最后一位校验位
      if (code.length == 18) {
        code = code.split("");
        // ∑(ai×Wi)(mod 11)
        // 加权因子
        const factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
        // 校验位
        const parity = [1, 0, "X", 9, 8, 7, 6, 5, 4, 3, 2];
        let sum = 0;
        let ai: any;
        let wi = 0;
        for (let i = 0; i < 17; i++) {
          ai = code[i];
          wi = factor[i];
          sum += ai * wi;
        }
        if (parity[sum % 11] != code[17]) {
          tip = "身份证校验位错误";
          pass = false;
        }
      }
    }
  }
  return { pass, tip };
};

export { getFormatDate, getIdcard, cnNewID, getRandom, isCardIDValid };
