const makeCanvasToPhoto = () => {
  let canvas: any = document.getElementById("mycanvs");
  let context = canvas.getContext("2d");
  let ratio = getPixelRatio(context); // 关键代码

  canvas.width = 420 * ratio; // 画布宽度
  canvas.height = 300 * ratio; // 画布高度
  let divWidth = 420 * ratio; // 用于内容居中
  let divHeight = 300 * ratio; // 用于内容居中

  const imgX = (canvas.width / 32) * 20;
  const imgY = canvas.height / 15;
  const imgW = (canvas.width / 32) * 10;
  const imgH = (canvas.height / 3) * 2;

  // 画矩形
  context.beginPath();
  context.fillRect(0, 0, divWidth, divHeight);
  context.closePath();

  // eslint-disable-next-line @typescript-eslint/no-var-requires
  let src1: any = require("../assets/front.png");

  console.log(src1.width);
  // eslint-disable-next-line @typescript-eslint/no-var-requires
  let src2: any;
  let pImg: any[] = [];
  const produce = () => {
    pImg.push(imgOnload(src1, "one"));
    pImg.push(imgOnload(src2, "two"));
    console.log(src2);
    console.log(pImg);
    Promise.all(pImg).then((resArr) => {
      resArr.map((item: any) => {
        console.log(item, item.src);
        if (item && item.img) {
          if (item.value === "one") {
            context.drawImage(item.img, 0, 0, divWidth, divHeight);
          } else if (item.value === "two") {
            context.drawImage(item.img, imgX, imgY, imgW, imgH);
            // let base64Obj = canvas.toDataURL("image/png", 1);
            // console.log(base64Obj);

            let y = new Date(ruleForm.brithDay).getFullYear();
            let m = new Date(ruleForm.brithDay).getMonth() + 1;
            let d = new Date(ruleForm.brithDay).getDate();

            // 文本
            const fontSize = canvas.width / 30;
            context.beginPath();
            context.font = `${fontSize}px bold`;
            context.fillStyle = "#1a1a1a";
            context.textAlign = "left";
            context.textBaseline = "middle";
            const nameX = (canvas.width / 32) * 5.5;
            const nameY = (canvas.height / 15) * 2.8;
            context.fillText(ruleForm.userName, nameX, nameY);

            const sexX = (canvas.width / 32) * 5.5;
            const sexY = (canvas.height / 15) * 4.7;
            context.fillText(ruleForm.sexCode, sexX, sexY);

            const nationX = (canvas.width / 32) * 12.5;
            const nationY = (canvas.height / 15) * 4.6;
            context.fillText(ruleForm.nation, nationX, nationY);

            const yX = (canvas.width / 32) * 5.5;
            const yY = (canvas.height / 15) * 6.5;
            const mX = (canvas.width / 32) * 10.5;
            const mY = (canvas.height / 15) * 6.5;
            const dX = (canvas.width / 32) * 13.6;
            const dY = (canvas.height / 15) * 6.5;

            context.fillText(y, yX, yY);
            context.fillText(m, mX, mY);
            context.fillText(d, dX, dY);

            const addressX = (canvas.width / 32) * 5.5;
            const addressY = (canvas.height / 15) * 8.5;

            let str = ruleForm.address;
            let arr = [];
            let s = "";
            for (let i = 0; i < str.length; i++) {
              if (s.length < 11 && i !== str.length - 1) {
                s += str[i];
              } else if (i === str.length - 1) {
                console.log(i, str.length);
                s += str[i];
                arr.push(s);
              } else {
                arr.push(s);
                s = "";
                s += str[i];
              }
            }

            let arr1 = [];

            for (let i = 0; i < arr.length; i++) {
              let obj = {
                str: arr[i],
                x: addressX,
                y: addressY + i * 30,
              };
              arr1.push(obj);
            }

            arr1.map((item) => {
              context.fillText(item.str, item.x, item.y);
            });

            const idcardX = (canvas.width / 32) * 10;
            const idcardY = (canvas.height / 15) * 13.4;

            context.fillText(ruleForm.idcard, idcardX, idcardY);

            context.closePath();

            context.drawImage(canvas, 0, 0, divWidth, divHeight);
            let base64Obj = canvas.toDataURL("image/png", 1);
            // console.log(base64Obj);
            let imgArr = base64Obj.split(",");
            imgUrl.value = imgArr[1];
          }
        }
      });
    });
  };
  if (ruleForm.headUrl.length === 1) {
    fileToBlob(ruleForm.headUrl[0].raw, (newFile: any) => {
      imgUrl.value = newFile;
      if (newFile) {
        if (newFile.indexOf(",") > -1) {
          src2 = newFile;
          console.log("test:", src2);
          produce();
        }
      }
    });
  } else {
    src2 = require("../assets/cardHead.png");
    produce();
  }
};

const getPixelRatio = (context: any) => {
  let backingStore =
    context.backingStorePixelRatio ||
    context.webkitBackingStorePixelRatio ||
    context.mozBackingStorePixelRatio ||
    context.msBackingStorePixelRatio ||
    context.oBackingStorePixelRatio ||
    context.backingStorePixelRatio ||
    1;
  return (window.devicePixelRatio || 1) / backingStore;
};

const imgOnload = (url: string, value: string) => {
  return new Promise((resolve) => {
    let img: any = new Image();
    img.crossorigin = "anonymous"; //解决画布污染问题
    img.src = url;
    img.onload = () => {
      resolve({
        img,
        value,
      });
    };
  });
};

const submitForm = async (formEl: FormInstance | undefined) => {
  if (!formEl) return;
  await formEl.validate((valid, fields) => {
    if (valid) {
      console.log("submit!", ruleForm);
      let idcard = "";
      let idCard16 = ruleForm.area[2];
      let idCard714 = ruleForm.brithDay.replace(/-/g, "");
      idcard = idCard16 + idCard714;
      let last: any;

      let arr1 = ["1", "3", "5"];
      let arr2 = ["2", "4", "6"];
      if (ruleForm.sexCode === "男") {
        let rand = Math.floor(Math.random() * arr1.length);
        // 随机从数组中取出某值（不会改变原数组）
        let data = arr1[rand];
        idcard = idcard + getRandom(10, 0);
        idcard = idcard + getRandom(10, 0) + data;
        last = cnNewID(idcard);
      } else {
        let rand = Math.floor(Math.random() * arr2.length);
        // 随机从数组中取出某值（不会改变原数组）
        let data = arr2[rand];
        idcard = idcard + getRandom(10, 0);
        idcard = idcard + getRandom(10, 0) + data;
        last = cnNewID(idcard);
      }

      idcard = idcard + last;

      let params = isCardIDValid(idcard);
      if (params.pass) {
        console.log(idcard);
        if (!ruleForm.idcard) {
          ruleForm.idcard = idcard;
        }
        makeCanvasToPhoto();
      } else {
        ElMessage.closeAll();
        ElMessage.error({
          message: params.tip,
          duration: 0,
        });
      }
    } else {
      console.log("error submit!", fields);
    }
  });
};

const resetForm = (formEl: FormInstance | undefined) => {
  if (!formEl) return;
  formEl.resetFields();
};

const generateIddcard = () => {
  let str = getIdcard();
  idcard.value = str;
};

const getJson = (val: string) => {
  const isJSON = (str: string) => {
    if (typeof str == "string") {
      try {
        JSON.parse(str);
        return true;
      } catch (e) {
        return false;
      }
    } else {
      return false;
    }
  };
  console.log(isJSON(val));
  if (isJSON(val)) {
    let data = JSON.parse(val);
    flowJSON.value = JSON.stringify(data, null, "  ");
    ElMessage.closeAll();
  } else {
    ElMessage.closeAll();
    ElMessage.error({
      message: "格式有误,请重新输入",
      duration: 0,
    });
  }
};

